import axios from 'axios';

const url = '/api/posts/'
const enurl = '/api/enposts/'

class PostService {
  // get Arabic posts
  static getPosts() {
    return new Promise((resolve, reject) => {
      axios.get(url).then((res) => {
        const data = res.data
        resolve(
          data.map(post => ({
            ...post,
            createdAt: new Date(post.createdAt)
          }))
        )
      })
      .catch((err) => {
        reject(err)
      })
    })
  }
  // creat arabic post
  static insertPost(markdown,title,description,formData) {
    return axios.post(url, {
      markdown,
      title,
      description,
      formData
    })
  }

  // update arabic post  
  static updatePost(slug,text,title,description){
      return axios.put(`${url}${slug}`,{
        text,
        title,
        description
      
      })
  }


  // delete arabic post
  static deletePost(slug) {
    return axios.delete(`${url}${slug}`)
  }
  ////////// ENG
  // get posts
  static engetPosts() {
  return new Promise((resolve, reject) => {
    axios.get(enurl).then((res) => {
      const data = res.data
      resolve(
        data.map(post => ({
          ...post,
          createdAt: new Date(post.createdAt)
        }))
      )
    })
    .catch((err) => {
      reject(err)
    })
  })
}
  // creat post
  static eninsertPost(markdown,title,description,formData) {
    return axios.post(enurl, {
      markdown,
      title,
      description,
      formData
    })
  }

  // update post  
  static enupdatePost(slug,text,title,description){
      return axios.put(`${enurl}${slug}`,{
        text,
        title,
        description
      
      })
  }

  // delete post
  static endeletePost(slug) {
    return axios.delete(`${enurl}${slug}`)
  }
}

export default PostService 