                <li>
                    <!-- <span data-aos="fade-up" data-aos-delay="200" data-aos-duration="1000" data-aos-anchor=".privacy-policy-content">Information Collection and Use</span> -->
                    <p data-aos="fade-up" data-aos-delay="400" data-aos-duration="1000" data-aos-anchor=".privacy-policy-content">
                        ويقوم التطبيق بالربط والوساطة في النقل والتوصيل حيث يتيح للمستفيدين إرسال شحناتهم بين المدن والمحافظات من خلال المسافرين.<br>
                        يقصد بالمصطلحات متى ما وردت في هذه الاتفاقية – اتفاقية سياسة الخصوصية - ما يلي ما لم يكن مراد السياق لمعنى 
                        آخر:<br>
                        التطبيق:<br>
                        منصة ( pass on) الإلكترونية التي من خلالها يتمكن المستخدم من الاستفادة من خدمات (pass on).<br>
                        الحساب:<br>
                        هو السجل –الصفحة- الخاص بالمستخدم المنشأ من أجل أن يتمكن المستخدم الاستفادة من  خدمات (pass on وتتاح له إمكانية طلب إرسال شحنة وإمكانية نقلها 
                        أو توصيلها.<br>
                        المرسل :<br>
                        هو الشخص المتعاقد مع شخص آخر يسمى الناقل لغرض نقل بضاعة إلى شخص 
                        آخر يسمى المرسل إليه. <br>
                        الناقل:<br>
                        هو الشخص الذي يقوم بنقل الشحنة من مدينة المرسل إلى مدينة المرسل إليه.<br>
                        المندوب:<br>
                        هو الشخص الذ يتم الاستعانة به لتوصيل الشحنة من نقطة الاستلام إلى الناقل <br>
                        أو من الناقل إلى نقطة الوصول.<br>
                        المستخدم:<br>
                        هو الشخص المدون بياناته الشخصية في الحساب، ويستخدم (pass on ) 
                        للاستفادة من خدماته سواءً كان ناقل أو مرسل أو مندوب.</p>
                </li>
                <!-- End privacy-policy-content li -->
                <!-- Start privacy-policy-content li -->
                <li>
                    <span data-aos="fade-up" data-aos-delay="600" data-aos-duration="1000" data-aos-anchor=".privacy-policy-content">أولاً: المقدمة</span>
                    <p data-aos="fade-up" data-aos-delay="800" data-aos-duration="1000" data-aos-anchor=".privacy-policy-content">
                        <br>
                        <ol class="policylist">
                            <li>يقصد (بشروط سياسة الخصوصية) طريقة جمع البيانات و طريقة حماية المعلومات الشخصية للمستخدمين، وتعد هذه السياسة بغاية الأهمية.</li>
                            <li>لا يعني تطبيق (pass on) أي معلومات شخصية لشخص محدد بعينه ، و إنما يتم جمع المعلومات الشخصية من الراغبين باستخدام تطبيق (pass on) بما في ذلك على سبيل المثال لا الحصر طلب الخدمات وتقديمها والتصفح أو القيام بالاتصال هاتفياً أو بواسطة البريد الالكتروني بفريق خدمة العملاء لدينا، و يعد تزويد المستخدم لنا بمعلوماته الشخصية موافقة منه بمعالجة هذه المعلومات وفقاً لبنود وشروط سياسة الخصوصية.</li>
                            <li>قد يتم تعديل شروط سياسة الخصوصية أو جزء منها في أي وقت، وسيتم نشر النسخة المعدلة على التطبيق. وتكون النسخة المعدلة من (سياسة الخصوصية) سارية المفعول اعتباراً من تاريخ نشرها، وبعد النشر يعتبر استمرار المستخدم في استخدام التطبيق التزاماً منه بالشروط والبنود الواردة في النسخة المعدلة من سياسة الخصوصية، ورضاً منه في تطبيق التعديلات على بياناتك الشخصية التي كنت قد زودتنا بها. </li>
                            <li>من خلال استخدام المستخدم لتطبيق (pass on) فإنه يوافق على شروط سياسة الخصوصية، وتعد سياسة الخصوصية اتفاقية ملزمة قانونياً، وإذا تمت إضافة أي ميزات أو أدوات جديدة إلى خدماتنا، فستخضع أيضا لهذه السياسة. </li>
                            <li>سنقوم بحفظ معلومات المستخدم الشخصية، وسيتم استخدامها والكشف عنها وفقا لما تسمح به الأنظمة والقوانين والأعراف.</li>
                            <li> سوف نقوم بطلب موافقة المستخدم دائماً قبل استخدام معلوماته الشخصية لأغراض غير تلك</li>
                        </ol>
                    </p>
                </li>
                <!-- End privacy-policy-content li -->
                <!-- Start privacy-policy-content li -->
                <li>
                    <span data-aos="fade-up" data-aos-delay="1000" data-aos-duration="1000"
                        data-aos-anchor=".privacy-policy-content">ثانياً: معلومات زوار التطبيق ومستخدمي الدعم</span>
                    <p data-aos="fade-up" data-aos-delay="1200" data-aos-duration="1000" data-aos-anchor=".privacy-policy-content"></p>
                    <br>
                    <ol >
                     <li>عند قيامك بزيارة تطبيق (pass on) أو عند قيامك بتثبيت التطبيق على جهازك نقوم بجمع معلومات عن الاسم وعنوان البريد الإلكتروني ومعلومات الجهاز ونظام التشغيل والمتصفح الذي تستخدمه واتصال الشبكة وعنوان البروتوكول الخاص بك (IP Address) ومعلومات شبكة الجوال بما في ذلك رقم الهاتف ومعرفات الجهاز، ونجمع معلومات مواقع الجهاز إذا كنت منحتنا صلاحية الوصول لموقعك، وقد نتلقى أيضا معلومات شخصية عند إرسال طلبات دعم أو طلبات أخرى إلى تطبيق (pass on)، كما نقوم بجمع نص المحادثة من مستخدمي دعم الدردشة.</li>
                     <li>نقوم بجمع رقم الهاتف وتسجيل المكالمات من مستخدمي الدعم عبر الهاتف، وذلك لقياس الأداء ورفع مستوى الجودة.</li>
                     <li>نقوم بجمع هذه المعلومات عند زيارتك لتطبيق (pass on)أو عند التواصل معنا إما عن طريق البريد الإلكتروني أو الويب أو الرسائل الفورية أو الهاتف كما نجمع أي معلومات إضافية قد تقدمها لنا، ويستخدم تطبيق (pass on) هذه المعلومات لخدمتك وتحسين الخدمات والإجابة عن أية أسئلة قد تكون لديك.</li>
                    </ol>
                </li>
                <!-- End privacy-policy-content li -->
                <!-- Start privacy-policy-content li -->
                <li>
