                        <li>نقوم باستخدام ملفات تعريف الارتباط للتعرف على جهازك وتزويدك بخدمات شخصية.</li>
                        <li>قد نستخدم تقنيات وأساليب التتبع الآلية على التطبيق أثناء إجراء الاتصالات معك أو أثناء استخدامك للتطبيق، وذلك لقياس الأداء وتحسين الخدمات.</li>
                        <li>نقوم بتحليل واستخدام المعلومات التي لدينا لتقييم وتحسين خدماتنا والبحث والتطوير واختبار الخدمات والميزات الجديدة، وإجراء أنشطة استكشاف الأخطاء وإصلاحها، ونقوم أيضا باستخدام المعلومات الخاصة بك للرد عليك وإفادتك عند اتصالك بنا</li>
                    </ol>
                    </p>
                </li>
                <!-- End privacy-policy-content li -->
                <!-- Start privacy-policy-content li -->
