                            <li>قد نقوم بمشاركة المعلومات الشخصية مع طرف ثالث للتحقيق أو لاتخاذ إجراءات بشأن الأنشطة الغير القانونية أو الاحتيالية المشتبه بها أو الحالات التي تنطوي على تهديدات محتملة للسلامة لأي شخص، أو في حالات انتهاكات بنود اتفاقية الشروط والأحكام أو سياسة الخصوصية أو حسب ما تقتضيه الأنظمة والقوانين.</li>
                            <li>قد نقوم أيضا بمشاركة المعلومات الشخصية مع الجهات التي تتملك تطبيق (pass on) مستقبلاً سواء بالاندماج أو الاستحواذ الكلي أو الجزئي، وفي حالة حدوث ذلك، فإننا سننشر إشعاراً على صفحتنا الرئيسية.	</li>
                        </ol>
                    </p>
                </li>
                <!-- End privacy-policy-content li -->
