                            <li>عند انتهاء علاقتنا معك سنواصل تخزين معلومات مجهولة المصدر مثل زيارات مواقع الويب بدون معرفات لتحسين خدماتنا.</li>
                            <li>نحتفظ بمعلوماتك بشكل آمن وذلك باتباع معايير عالية في إدارة أمن المعلومات لحماية المعلومات الحساسة مثل المعلومات المالية وتفاصيل أي معلومات شخصية أخرى، وتطبق أنظمة أمن المعلومات الخاصة بنا على الأشخاص والعمليات وأنظمة تكنولوجيا المعلومات على أساس إدارة المخاطر، ويقوم طرف ثالث مؤهل لإدارة وسائل وآلية الدفع وتدقيقها.</li>
                            <li>نؤكد لك أنه لا توجد طريقة إرسال عبر الإنترنت أو طريقة تخزين إلكتروني آمنة 100% ، لذلك لا يمكننا ضمان الأمان المطلق لمعلوماتك.</li>
                            <li>يمكنك الاحتفاظ بجميع معلوماتك الشخصية ومعلومات سجل عملياتك ويمكنك الدخول إليها في أي وقت، كما نمنحك صلاحيات محدودة من إعدادات حسابك للسماح لك بتصحيح أو تعديل أو حذف أو الحد من استخدام المعلومات الشخصية الخاصة بك.</li>
                            <li>قد يترتب على قيامك بحذف أو تقييد استخدام المعلومات الشخصية الخاصة بك، تعطيل الاستفادة من خدماتنا بالشكل المرضي.	</li>
                        </ol>
                    </p>
